$(document).ready(function() {
    //set initial state.
    $('#showOnlyPodcasts').val(this.checked);

    $('#showOnlyPodcasts').change(function() {
        if(this.checked) {
            var returnVal = confirm("Are you sure?");
            $(this).prop("checked", returnVal);
        }
        $('#textbox1').val(this.checked);
    });
});
